import React from 'react';
import './About.css';
import Avatar from '../../media/avatar.png';

export const About = () => {
    return (
        <div className="about-container">
            <div className="about-desc">
                <h3>About me</h3> 
                <p>Hi, I'm Andres. Nice to meet you. I am passionate about the world of web development, I enjoy creating the best solutions to problems, and to achieve this I have become a Front-end Developer.</p>
            </div>
            <div className="about-img">
                <img src={Avatar} alt='about'/>
            </div>
        </div>
    )
}
export default About;