import React from 'react';
import './Cover.css';
import coverVideo from '../../media/video.mp4'

export const Cover = () => {
    return (
        <div className="cover-container">
            <video className="video" src={coverVideo} autoPlay loop muted />
            <h1>Andres Diaz</h1>
            <p>Frontend Developer | Frelancer</p>
        </div>
    )
} 
export default Cover;